#root {
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  min-height: 100vh
}
body {
  font-size: 16px;
  background-color: #e2e8f0;
  font-family: Roboto,sans-serif;
  font-style: normal;
  margin: 0;
  padding: 0
}
@media (max-width:576px) {
  body {
    font-size: 14px
  }
}
*,
:after,
:before {
  box-sizing: border-box
}
.footer {
  color: #27303f;
  font-size: .8em;
  text-align: center;
  padding: 25px 0;
  width: 100%;
  margin-top: auto
}
.error {
  width: 100%;
  margin-top: 20vh;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap
}
.error-status {
  flex-grow: 1;
  flex-basis: 100%;
  text-align: center;
  font-size: 3em;
  font-weight: 700;
  margin-bottom: .2em
}
.error-message {
  text-align: center;
  font-size: 1.2em
}
.header {
  width: 100%;
  position: absolute;
  z-index: 100;
  top: 0;
  left: 0;
  padding: 30px 0;
  height: 90px
}
.header-nav {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 30px
}
.header-nav .right-side > :not(:last-child) {
  margin-right: 45px
}
.header-nav-link {
  text-transform: uppercase;
  text-decoration: none;
  font-size: 1.3em;
  color: #27303f;
  position: relative
}
.header-nav-link::after {
  content: "";
  position: absolute;
  top: 110%;
  left: 0;
  width: 100%;
  height: 1px;
  transform: scale(0);
  transform-origin: center;
  transition: transform .2s ease-out;
  background-color: #27303f
}
.header-nav-link:hover::after {
  transform: scale(1)
}
.loader {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #e2e8f0;
  margin-top: 50px
}
.loader > * {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background-color: #3b485f
}
.loader > :not(:last-child) {
  margin-right: 15px
}
.loader > :nth-child(1) {
  animation: loader 3s ease-in-out infinite 0s
}
.loader > :nth-child(2) {
  animation: loader 3s ease-in-out infinite .2s
}
.loader > :nth-child(3) {
  animation: loader 3s ease-in-out infinite .4s
}
.loader > :nth-child(4) {
  animation: loader 3s ease-in-out infinite .6s
}
@keyframes loader {
  0% {
    opacity: 0;
    transform: scale(.4);
    background: #27303f
  }
  25% {
    opacity: 1;
    transform: scale(1.6);
    background: #62789e
  }
  50% {
    opacity: 0;
    transform: scale(.4);
    background: #0d72fc
  }
  75% {
    opacity: 1;
    transform: scale(1.6);
    background: #a4cafe
  }
  100% {
    opacity: 0
  }
}
.main {
  padding-top: 95px;
  width: 100%;
  height: 100%
}
.main .container {
  margin: 0 auto;
  max-width: 55%
}
@media (max-width:1200px) {
  .main .container {
    max-width: 70%
  }
}
@media (max-width:992px) {
  .main .container {
    max-width: 90%
  }
}
@media (max-width:576px) {
  .main .container {
    margin: 0 10px;
    max-width: 100%
  }
}
.match {
  display: flex;
  align-items: center;
  width: 100%;
  padding: 15px;
  border-radius: 15px;
  color: #27303f;
  background-color: #fff;
  box-shadow: 0 1px 7px rgba(0,0,0,.25);
  transition: .1s;
  justify-content: flex-start;
  gap: 2%
}
@media (max-width:576px) {
  .match {
    border-radius: 10px;
    padding: 10px 8px
  }
}
.match:not(:last-child) {
  margin-bottom: 20px
}
.match-status {
  padding: 4px 8px;
  border-radius: 6px
}
.match-status.postponed,
.match-status.scheduled {
  background-color: #ffd60a
}
.match-status.finished {
  background-color: #a4cafe
}
.match-status.canceled,
.match-status.postponed,
.match-status.suspended {
  background-color: #f17266
}
.match-status.live,
.match-status.paused {
  background-color: #6bc740
}
.match-teams {
  display: flex;
  align-items: center
}
.match-teams > :nth-child(2) {
  margin: 0 10px
}
@media (max-width:576px) {
  .match-teams > :nth-child(2) {
    margin: 0 5px
  }
}
.match-teams .away-team,
.match-teams .home-team {
  display: flex;
  align-items: center;
  cursor: pointer
}
.match-teams .away-team:hover,
.match-teams .home-team:hover {
  color: #3f8ffd
}
@media (max-width:768px) {
  .match-teams .long {
    display: none
  }
}
.match-teams .short {
  display: none
}
@media (max-width:768px) {
  .match-teams .short {
    display: inline
  }
}
.match-teams .long.winner,
.match-teams .short.winner {
  font-weight: 700
}
.match-teams .icon-container {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 5px
}
.match-teams .icon-container > img {
  width: 24px;
  height: 24px
}
@media (max-width:576px) {
  .match-teams .icon-container > img {
    width: 20px;
    height: 20px
  }
}
.match-score {
  margin-right: 15px;
  margin-left: auto
}
@media (max-width:576px) {
  .match-score {
    margin-right: 5px
  }
}
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 200;
  margin: 0;
  width: 100vw;
  height: 100vh
}
.pagination-controls-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: nowrap;
  margin-top: 30px
}
.pagination-control {
  background-color: #fff;
  color: #27303f;
  border-radius: 3px;
  padding: 5px 6px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  user-select: none
}
@media (max-width:576px) {
  .pagination-control {
    border-radius: 4px;
    padding: 7px 8px
  }
}
.pagination-control:not(:nth-last-child(2)) {
  margin-right: 5px
}
.pagination-control.current {
  background-color: #a4cafe
}
.pagination-control:hover {
  background-color: #a4cafe
}
.pagination-control-arrow {
  display: flex;
  align-items: center;
  justify-content: center;
  user-select: none;
  border: none;
  background-color: transparent;
  cursor: pointer;
  padding: 5px
}
.pagination-control-arrow > img {
  height: 16px;
  width: auto
}
.search {
  background-color: #fff;
  border-radius: 25px;
  width: 100%;
  height: 50px;
  box-shadow: 0 1px 7px rgba(0,0,0,.25);
  border: 1px solid transparent;
  transition: .1s;
  padding-left: .5em;
  padding-right: 1em
}
.search.focus {
  border-color: #a4cafe
}
@media (max-width:576px) {
  .search {
    height: 40px;
    border-radius: 20px
  }
}
.search-content {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: flex-start
}
.search-icon-container {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 30px;
  height: 30px;
  margin-right: .5em
}
.search-icon-container img {
  width: 20px;
  height: 20px;
  user-select: none
}
.search-input {
  font-size: inherit;
  margin: 0;
  padding: 0;
  background-color: transparent;
  border: none;
  flex-basis: 100%;
  flex-grow: 1
}
.search-input:focus {
  outline: 0
}
.team {
  display: flex;
  align-items: center;
  width: 100%;
  padding: 15px;
  border-radius: 15px;
  color: #27303f;
  background-color: #fff;
  box-shadow: 0 1px 7px rgba(0,0,0,.25);
  transition: .1s;
  cursor: pointer
}
@media (max-width:576px) {
  .team {
    border-radius: 10px;
    padding: 10px 8px
  }
}
.team:not(:last-child) {
  margin-bottom: 20px
}
.team:hover {
  background-color: #bdd9fe
}
.team-area,
.team-info {
  display: flex;
  align-items: center;
  justify-content: flex-start
}
.team-area .icon-container,
.team-info .icon-container {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 10px
}
.team-area .icon-container > img,
.team-info .icon-container > img {
  width: 24px;
  height: 24px
}
@media (max-width:768px) {
  .team-info .long {
    display: none
  }
}
.team-info .short {
  display: none
}
@media (max-width:768px) {
  .team-info .short {
    display: inline
  }
}
.team-area {
  margin-left: auto
}
.date-range-piker {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 8px;
  position: absolute;
  left: 0;
  top: 0;
  padding: 10px 20px;
  background-color: #fff;
  box-shadow: 0 1px 7px rgba(0,0,0,.25);
  border-radius: 10px
}
.date-piker {
  position: relative;
  padding: 1.7em 0 5px 0
}
.date-piker label {
  text-transform: capitalize;
  position: absolute;
  top: 10%
}
.date-piker input[type=date] {
  font-size: 16px;
  color: #27303f;
  font-family: inherit;
  padding: 0;
  border-radius: 5px;
  border: none;
  background-color: transparent
}
.date-piker input[type=date]:focus {
  outline: 0
}
.date-piker input[type=date]::-webkit-calendar-picker-indicator {
  margin-left: -28px
}
.date-range-controls :nth-child(1) {
  margin-right: 10px
}
.date-range-controls button {
  text-transform: capitalize;
  padding: 8px 20px;
  border-radius: 8px;
  border: none
}
.date-range-controls button:focus {
  outline: 0
}
.date-range-controls .apply-filter {
  color: #fff;
  background-color: #27303f;
  box-shadow: 0 1px 7px rgba(39,48,63,.25)
}
.date-range-controls .clear-filter {
  color: #27303f;
  background-color: #e2e8f0;
  box-shadow: 0 1px 7px rgba(226,232,240,.25)
}
.competition-info {
  display: flex;
  align-items: center;
  margin: 30px 0
}
@media (max-width:576px) {
  .competition-info {
    flex-wrap: wrap
  }
}
.competition-title {
  margin: 0;
  font-size: 1.5em
}
@media (max-width:576px) {
  .competition-title {
    text-align: center;
    flex-basis: 100%;
    flex-grow: 1;
    margin: 0 10px
  }
}
.section-controls {
  margin-left: auto;
  display: flex;
  align-items: center
}
@media (max-width:576px) {
  .section-controls {
    justify-content: center;
    margin-top: 15px;
    flex-basis: 100%;
    flex-grow: 1
  }
}
.section-controls .section-control {
  padding: 10px 15px;
  min-width: 80px;
  color: #27303f;
  background-color: #fff;
  border: none;
  border-radius: 10px;
  box-shadow: 0 1px 7px rgba(0,0,0,.25);
  cursor: pointer;
  text-transform: uppercase;
  transition: .1s
}
@media (max-width:576px) {
  .section-controls .section-control {
    padding: 10px 8px
  }
}
.section-controls .section-control.selected {
  color: #fff;
  background-color: #27303f;
  box-shadow: 0 1px 7px rgba(39,48,63,.25)
}
.section-controls .section-control:not(:last-child) {
  margin-right: 15px
}
@media (max-width:576px) {
  .section-controls .section-control:not(:last-child) {
    margin-right: 10px
  }
}
.section-controls .section-control:hover {
  box-shadow: 0 1px 20px 2px rgba(164,202,254,.7);
  background-color: #a4cafe
}
.section-controls .section-control:hover.selected {
  color: #27303f
}
.competition-detail-list {
  margin: 0;
  padding: 0;
  list-style: none
}
.competitions {
  margin-top: 30px;
  padding: 0;
  list-style: none
}
.team-detail-list {
  margin: 0;
  padding: 0;
  list-style: none
}
.team-detail-team-name {
  font-size: 1.5em;
  margin: 30px 15px
}
.filter-button {
  background: 0 0;
  border: none;
  display: flex;
  align-items: center;
  justify-content: center
}
.filter-button:focus {
  outline: 0
}
.filter-button img {
  width: 18px;
  height: 18px
}
.no-search-results {
  width: 100%;
  text-align: center;
  margin: 40px 0;
  font-size: 1.5em;
  font-weight: 700
}
.competition {
  display: flex;
  align-items: center;
  width: 100%;
  padding: 15px;
  border-radius: 15px;
  color: #27303f;
  background-color: #fff;
  box-shadow: 0 1px 7px rgba(0,0,0,.25);
  transition: .1s;
  justify-content: space-between;
  cursor: pointer
}
@media (max-width:576px) {
  .competition {
    border-radius: 10px;
    padding: 10px 8px
  }
}
.competition:not(:last-child) {
  margin-bottom: 20px
}
.competition:hover {
  background-color: #bdd9fe
}
.competition-name {
  display: flex;
  align-items: center;
  flex-basis: 50%
}
@media (max-width:576px) {
  .competition-name .area {
    display: none
  }
}
.competition-name .icon-container {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 10px
}
.competition-name .icon-container > img {
  width: 20px;
  height: 20px
}
.season-info {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  flex-basis: 50%
}
.season-info > * {
  text-transform: capitalize
}
.season-info > :not(:last-child) {
  margin-right: 20px
}
@media (max-width:576px) {
  .season-info > :not(:last-child) {
    margin-right: 10px
  }
}
.season-info .tier {
  text-align: right;
  flex-basis: 30%
}
@media (max-width:576px) {
  .season-info .tier {
    text-align: left
  }
}
.season-info .matchday {
  flex-basis: 55%;
  position: relative;
  padding-left: 15%;
  text-align: left
}
@media (max-width:576px) {
  .season-info .matchday {
    padding-left: 0
  }
}
@media (max-width:320px) {
  .season-info .matchday {
    text-align: center
  }
}